<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BetterScroll from 'better-scroll'

export default {
  name: "ScrollComponent",
  data() {
    return {
      scroll: null,
      scrollY:0
    }
  },
  mounted() {
    if (!this.scroll) {
      this.scroll = new BetterScroll(this.$refs.wrapper, {
        probeType: 3,
        pullUpLoad: true,
        click: true
      })
      this.scroll.on('scroll', ({y}) => {
        this.scrollY = y;
        this.$emit('doTabFixed',y)
        if (-y > 100) {
          this.$emit('doCanBackTop', true)
        } else {
          this.$emit('doCanBackTop', false)
        }

      })
      this.scroll.on('pullingUp', () => {
        this.$emit('pullingUp')

      })
    }

  },
  beforeDestroy() {
    this.scroll.destroy()
  },
  methods:{
    refresh(){
      this.scroll.refresh()
    }
  }


}
</script>

