  getTabs: function (page) {
    // return requestHelper.request({
    //   url: config.api.url + 'get_tabs'
    // })

    const res = []

    for (let i = 1; i <= 10; i++) {
      const price = '128.00'

      let id = 0
      if (page===1){
        id = i
      } else{
        id = (page - 1)*10 + i
      }
      res.push(
        {
          'id': id,
          'pic': config.imgPath + 'tab.jpg',
          'title': '    150*70*40',
          'price': price
        })
    }
    return res
  }
